.letter-btn {
    background-color: white;
    border: 2px solid #d3d6da;
    color: #333;
    display: inline-block;
    font-size: 1.2em;
    font-weight: bold;
    font-family: 'Franklin Gothic Medium', 'Arial', sans-serif;
    height: 45px;
    width: 45px;
    margin: 2.5px;
    padding: 5px 10px;
    text-align: center;
    text-decoration: none;
}

@media (max-width: 768px) {
    .letter-btn {
        height: 50px;
        width: 50px;
    }
}

@media (max-width: 480px) {
    .letter-btn {
        height: 55px;
        width: 55px;
    }
}